import {useEffect, useState} from "react";
import {Helper} from "../../constants/Helper";
import {Table} from "react-bootstrap";
import {useTranslation} from "react-i18next";
import i18next from "../../constants/Locale";
import {getDaysInMonth} from "date-fns";
import {useLoaderContext} from "../../hooks/LoaderContext";

const fetchConfig = {
    headers: new Headers({
        Authorization: `Bearer ${window.token}`
    }),
    credentials: "include",
    mode: "cors"
}

const Calendar2 = (props) => {
    const {index, lang} = props
    const token = window.token
    const [employees, setEmployees] = useState([])
    const {t} = useTranslation()

    useEffect(() => {
        i18next.changeLanguage(lang)
        fetch(`${Helper.url}/api/coworker/calendar`, fetchConfig)
            .then(response => response.json())
            .then(result => {
                setEmployees(result)
            })
            .catch(error => console.error(error))
    }, [index]);

    return (
        <div>
            <Table striped={true} hover={true}>
                <thead>
                <tr>
                    <th rowSpan={2} className={"align-middle text-left"}>{t("calendar.back")}</th>
                    <th colSpan={getDaysInMonth(new Date())} className={"text-center"}>Text</th>
                    <th rowSpan={2} className={"align-middle text-right"}>{t("calendar.next")}</th>
                </tr>
                <tr>
                    {Array.from({length: getDaysInMonth(new Date())}, (_, i) => i + 1).map(item => (
                        <th key={`day-${item}`}>{item}</th>
                    ))}
                </tr>
                </thead>
                <tbody>
                {employees.map(employee => {
                    console.log(employee)
                    return (
                        <tr key={`employee-${employee.id}`}>
                            <td>{employee?.user?.profile?.family} {employee?.user?.profile?.name}</td>
                        </tr>
                    )
                })}
                </tbody>
            </Table>
        </div>
    )
}

export default Calendar2