import React, {useEffect, useState} from "react";
import {useCalendarContext} from "../../hooks/CalendarContext";
import {getDaysInMonth} from "date-fns";
import {CalendarBody, CalendarFooter, CalendarHeader} from "./";

const CalendarTable = React.memo(({onModal, trigger, setHoverData, isFinance, currentDate, onAdd}) => {
    const {month, year, getHours} = useCalendarContext();
    const [days, setDays] = useState(0);
    const [info, setInfo] = useState([]);

    useEffect(() => {
        setDays(getDaysInMonth(new Date(year, month)));
    }, [month, year]);

    useEffect(() => {
        getHours(year, month).then(setInfo);
    }, [trigger, year, month, getHours]);

    return (
        <table className="table table-striped table-hover">
            <CalendarHeader days={days} />
            <CalendarBody
                data={info}
                days={days}
                onModal={onModal}
                setHoverData={setHoverData}
                isFinance={isFinance}
                currentDate={currentDate}
                onAdd={onAdd}
            />
            <CalendarFooter
                data={info}
                days={days}
                setHoverData={setHoverData}
                isFinance={isFinance}
            />
        </table>
    );
});

export {CalendarTable}