import React, {useEffect, useRef, useState, useCallback} from "react";
import "./Calendar.css"
import {useTranslation} from "react-i18next";
import i18next from "../../constants/Locale";
import {CalendarTable, Popup, MyModal} from "./";
import {useCalendarContext} from "../../hooks/CalendarContext";
import {AddModal} from "./AddModal";
import 'bootstrap/dist/css/bootstrap.min.css';

// Оптимизированные компоненты
const Calendar = ({lang}) => {
    const [modalOpen, setModalOpen] = useState(false);
    const [addModalOpen, setAddModalOpen] = useState(false)
    const [trigger, setTrigger] = useState(0);
    const [modalData, setModalData] = useState([]);
    const [hoverData, setHoverData] = useState(null);
    const [isFinanceMode, setIsFinanceMode] = useState(false);
    const {t} = useTranslation();
    const currentDate = useRef(new Date()).current;
    const {setHours, setLanguage} = useCalendarContext()

    useEffect(() => {
        setLanguage(lang)
        i18next.changeLanguage(lang)
    }, [lang, setLanguage]);

    const handleModalOpen = useCallback((data) => {
        setModalData(data);
        setModalOpen(true);
    }, []);

    const handleConfirm = useCallback((data) => {
        setHours(data)
            .then(() => setTrigger(prev => prev + 1))
            .finally(() => setModalOpen(false));
    }, [setHours]);

    const toggleMode = useCallback(() => setIsFinanceMode(prev => !prev), []);

    const handleNewHours = useCallback((data) => {
        setModalData(data)
        setAddModalOpen(true)
    }, [])

    return (
        <div id="my-calendar">
            <div className="mode">
                <input
                    type="checkbox"
                    id="mode"
                    checked={isFinanceMode}
                    onChange={toggleMode}
                />
                <label htmlFor="mode">{t("calendar.mode_finance")}</label>
            </div>

            <CalendarTable
                onModal={handleModalOpen}
                trigger={trigger}
                setHoverData={setHoverData}
                isFinance={isFinanceMode}
                currentDate={currentDate}
                onAdd={handleNewHours}
            />

            <MyModal
                show={modalOpen}
                data={modalData}
                onClose={() => setModalOpen(false)}
                onConfirm={handleConfirm}
                onAdd={(state) => setAddModalOpen(state)}
            />

            <AddModal show={addModalOpen} onClose={() => {setAddModalOpen(false)}} data={modalData} onSave={() => {
                setAddModalOpen(false)
                setModalOpen(false)
                window.location.reload()
            }} />

            {/*<Popup data={hoverData} isFinance={isFinanceMode} />*/}
        </div>
    );
};

export default React.memo(Calendar);