import React from "react";
import {createPortal} from "react-dom";

const modalElement = document.getElementById("modal");
export default function ModalExample({visible, children, ...props}) {
    return (
        <>
            {createPortal(
                <div className={["modal", visible ? "show" : ""].join(" ")}>
                    {children}
                </div>,
                modalElement
            )}
        </>
    )
}