import React, {useState} from "react";
import {useTranslation} from "react-i18next";
import {CalendarRow} from "./";

const CalendarBody = React.memo(({data, days, onModal, setHoverData, isFinance, currentDate, onAdd}) => {
    const {t} = useTranslation();
    const [searchText, setSearchText] = useState("");
    return (
        <tbody>
        <tr>
            <td colSpan={days + 2} className="py-2 px-2">
                <input
                    type="text"
                    className="form-control"
                    placeholder={t("calendar.search_text")}
                    onChange={(e) => setSearchText(e.target.value)}
                />
            </td>
        </tr>
        {data?.map((element, index) => (
            <CalendarRow
                key={`row-${element.user.id}-${index}`}
                index={index}
                element={element}
                days={days}
                onModal={onModal}
                setHoverData={setHoverData}
                searchText={searchText}
                isFinance={isFinance}
                currentDate={currentDate}
                onAdd={onAdd}
            />
        ))}
        </tbody>
    );
});

export {CalendarBody}