import React, {useMemo} from "react";
import {useCalendarContext} from "../../hooks/CalendarContext";
import {CalendarCell, TotalCell} from "./";

const CalendarRow = React.memo(({element, days, index, onModal, onAdd, setHoverData, searchText, isFinance, currentDate}) => {
    const {month, year} = useCalendarContext();
    // Оптимизация поиска
    const isVisible = useMemo(() => {
        if (!searchText) return true;
        const searchStr = `${element.user.profile.name} ${element.user.profile.family} ${element.user.profile.surname} ${element.user.username}`.toLowerCase();
        return searchStr.includes(searchText.toLowerCase());
    }, [searchText, element]);

    if (!isVisible) return null;

    return (
        <tr key={`coworker-${element.id}`} className={index % 2 ? 'bg-default' : ""}>
            <td>{element.user?.profile?.family} {element.user?.profile?.name}</td>
            {Array.from({length: days}, (_, i) => (
                <CalendarCell
                    key={`cell-${i}`}
                    index={i}
                    element={element}
                    currentDate={currentDate}
                    onClick={onModal}
                    setHoverData={setHoverData}
                    isFinance={isFinance}
                    onAdd={onAdd}
                />
            ))}
            <TotalCell
                element={element}
                month={month}
                year={year}
                setHoverData={setHoverData}
                isFinance={isFinance}
            />
        </tr>
    );
});

export {CalendarRow}