import React from 'react';
import ReactDOM from 'react-dom/client';
import reportWebVitals from './reportWebVitals';
import {
    Calendar,
    Calendar2,
    CoworkerForm,
    CoworkerView,
    DynamicTable,
    YandexMaps,
    YandexMapsView
} from "./components";
import {CalendarProvider, DynamicTableProvider, LoaderProvider} from "./hooks";
import {CoworkerProvider} from "./hooks/useCoworkerForm";

const list = document.getElementsByClassName("calendar");
if (list) {
    // @ts-ignore
    for (const element of list) {
        const domElement = ReactDOM.createRoot(element);
        domElement.render(
            <CalendarProvider>
                <Calendar {...element.dataset} />
            </CalendarProvider>
        )
    }
}

const calendar = document.getElementsByClassName("calendar_v2");
if (calendar) {
    // @ts-ignore
    for (const element of calendar) {
        const domElement = ReactDOM.createRoot(element);
        domElement.render(
            <CalendarProvider>
                <Calendar2 {...element.dataset} />
            </CalendarProvider>
        )
    }
}

const dTables = document.getElementsByClassName("dynamicTable");
if (dTables) {
    // @ts-ignore
    for (const table of dTables) {
        const domElement = ReactDOM.createRoot(table);
        domElement.render(
            <DynamicTableProvider>
                <DynamicTable {...table.dataset} />
            </DynamicTableProvider>
        )
    }
}

const profileContainer = document.getElementById('coworker-view')
if (profileContainer) {
    // @ts-ignore
    const domElement = ReactDOM.createRoot(profileContainer);
    domElement.render(
        <React.StrictMode>
            <CoworkerView {...profileContainer.dataset} />
        </React.StrictMode>
    )
}

const coworkerForm = document.getElementById('coworker-form')
if (coworkerForm) {
    const dom = ReactDOM.createRoot(coworkerForm)
    dom.render(
        <React.StrictMode>
            <LoaderProvider>
                <CoworkerProvider>
                    <CoworkerForm {...coworkerForm.dataset} />
                </CoworkerProvider>
            </LoaderProvider>
        </React.StrictMode>
    )
}

const yandexMaps = document.getElementById('yandex-maps')
if (yandexMaps) {
    const dom = ReactDOM.createRoot(yandexMaps)
    dom.render(
        <React.StrictMode>
            <LoaderProvider>
                <YandexMaps {...yandexMaps.dataset} />
            </LoaderProvider>
        </React.StrictMode>
    )
}

const yandexMapsView = document.getElementById('yandex-maps-view')
if (yandexMapsView) {
    const dom = ReactDOM.createRoot(yandexMapsView)
    dom.render(
        <React.StrictMode>
            <LoaderProvider>
                <YandexMapsView {...yandexMapsView.dataset} />
            </LoaderProvider>
        </React.StrictMode>
    )
}
