import React from "react";
import {useTranslation} from "react-i18next";
import {Modal, Accordion, Button, Card} from "react-bootstrap";
import {Helper} from "../../constants/Helper"

const MyModal = React.memo(({show, data, onClose, onAdd}) => {
    const {t} = useTranslation();
    return (
        <Modal show={show} onHide={onClose} size="md" centered={true}>
            <Modal.Header closeButton={true}>
                <Modal.Title>{Helper.dateFromString(data[0]?.date)?.toLocaleDateString()}</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <Accordion defaultActiveKey={0}>
                    {data?.map((item, index) => {
                        console.log(item)
                        return (
                            <Accordion.Item eventKey={index} key={`accordion-${index}`}>
                                <Accordion.Header className={item.is_payed ? "paid" : "unpaid"}>{t('calendar.order')} #{item.order_id} ({item.is_payed ? t('calendar.is_paid') : t('calendar.is_not_paid')})</Accordion.Header>
                                <Accordion.Body>
                                    <div className={'d-flex flex-row justify-content-between px-3 bg-secondary-subtle'}>
                                        <div>{t('calendar.cost')}</div>
                                        <div>{item.price}</div>
                                    </div>
                                    <div className={'d-flex flex-row justify-content-between px-3'}>
                                        <div>{t('calendar.popup_count')}</div>
                                        <div>{item.count}</div>
                                    </div>
                                    <div className={'d-flex flex-row justify-content-between px-3 bg-secondary-subtle'}>
                                        <div>{t('calendar.popup_status')}</div>
                                        <div>{item.is_payed ? t('calendar.is_paid') : t('calendar.is_not_paid')}</div>
                                    </div>
                                    <div className={'d-flex flex-row justify-content-between px-3'}>
                                        <div>{t('calendar.amount')}</div>
                                        <div>{item.credit > 0 ? item.credit : item.debit}</div>
                                    </div>
                                    <div className={'d-flex flex-row justify-content-between px-3 bg-secondary-subtle'}>
                                        <div>{t('calendar.start_time')}</div>
                                        <div>{item.start_time?.split(' ')[1] ?? t('not-set')}</div>
                                    </div>
                                    <div className={'d-flex flex-row justify-content-between px-3'}>
                                        <div>{t('calendar.stop_time')}</div>
                                        <div>{item.stop_time?.split(' ')[1] ?? t('not-set')}</div>
                                    </div>
                                </Accordion.Body>
                            </Accordion.Item>
                        )
                    })}
                </Accordion>
            </Modal.Body>
            <Modal.Footer>
                <div className={'d-flex flex-row justify-content-between w-100'}>
                    <div>
                        <Button variant="danger" onClick={onClose}>
                            {t("calendar.cancel")}
                        </Button>
                    </div>
                    <div>
                        <Button value={'primary'} onClick={() => {
                            onAdd(true)
                        }}>{t('calendar.add')}</Button>
                    </div>
                </div>
            </Modal.Footer>
        </Modal>
    );
});

export {MyModal}