import {Col, Row, Nav, Tab, Table, Button} from "react-bootstrap";
import React, {useEffect, useState} from "react";
import {Helper} from "../../constants/Helper"
import {useTranslation} from "react-i18next";
import "./CoworkerView.css"
import i18next from "i18next";
import {PatternFormat} from "react-number-format";

const ProfilePane = ({model, botName}) => {
    const {t} = useTranslation()
    const [toasts, setToasts] = useState([])
    return (
        <>
            <Table striped={true} hover={true}>
                <tbody>
                <tr>
                    <td>{t('family')}</td>
                    <td>{model?.family}</td>
                </tr>
                <tr>
                    <td>{t('name')}</td>
                    <td>{model?.name}</td>
                </tr>
                <tr>
                    <td>{t('surname')}</td>
                    <td>{model?.surname}</td>
                </tr>
                <tr>
                    <td>{t('birthday')}</td>
                    <td>{(new Date(model?.birthday ?? 0)).toLocaleDateString()}</td>
                </tr>
                <tr>
                    <td>{t('phone')}</td>
                    <td>{model?.phone &&
                        <PatternFormat displayType={"text"} format={"+# (###) ###-##-##"} allowEmptyFormatting
                                       value={model?.phone}/>}</td>
                </tr>
                </tbody>
            </Table>
            <Row>
                <Col sm={3}>
                    <Button variant={"outline-primary w-100"} onClick={() => {
                        navigator.clipboard?.writeText(`https://t.me/${botName}?start=${model.id}`).then(r => {
                            setToasts([...toasts, {
                                title: "Notification",
                                message: t('telegram_link_copy_successfully', {name: botName})
                            }])
                        })
                    }}>{t("invite_telegram_link")}</Button>
                </Col>
                <Col sm={3}>
                    <Button variant={"outline-success w-100"} onClick={() => {
                        navigator.clipboard.writeText(`https://amgcompany.ru/app/android/coworker.apk`).then(r => {
                            setToasts([...toasts, {title: "Notification", message: t('app_link_copy_successfully')}])
                        })
                    }}>{t("invite_app_link")}</Button>
                </Col>
            </Row>
        </>
    )
}

const AccountPane = ({model}) => {
    const {t} = useTranslation()
    return (
        <Table striped={true} hover={true}>
            <tbody>
            <tr>
                <td>{t('username')}</td>
                <td>{model?.username}</td>
            </tr>
            <tr>
                <td>{t('email')}</td>
                <td>{model?.email}</td>
            </tr>
            <tr>
                <td>{t('status')}</td>
                <td>{Helper.status[model?.status]}</td>
            </tr>

            </tbody>
        </Table>
    )
}

const AdvancedPane = ({model}) => {
    const {t} = useTranslation()
    const fetchConfig = {
        headers: new Headers({
            Authorization: `Bearer ${window.token}`
        })
    }
    const getCategory = async (category_id) =>
        await fetch(`${Helper.url}/api/category/view?id=${category_id}`, fetchConfig)
            .then(response => response.json())
    const getProperty = async (property_id) =>
        await fetch(`${Helper.url}/api/property/view?id=${property_id}`, fetchConfig)
            .then(response => response.json())
    const getDimension = async (dimension_id) =>
        await fetch(`${Helper.url}/api/dimension/view?id=${dimension_id}`, fetchConfig)
            .then(response => response.json())
    const [result, setResult] = useState({})
    return (
        <Table striped={true} hover={true}>
            <tbody>
            {model?.userProperties?.length !== 0 && model?.userProperties?.map((userProperty, index) => {
                const category = getCategory(userProperty.category_id)
                const property = getProperty(userProperty.property_id)
                const dimension = getDimension(userProperty.dimension_id)
                Promise.all([category, property, dimension])
                    .then(([category, property, dimension]) => {
                        setResult({
                            category: category,
                            property: property,
                            dimension: dimension
                        })
                    })
                return (
                    <tr key={`advanced-row-${index}`}>
                        <td>{result.category?.title}</td>
                        <td>{result.property?.title} {userProperty.value} {result.dimension?.title}</td>
                    </tr>
                )
            })}
            {model?.userProperties?.length === 0 && <tr>
                <td className={"text-center"}>{t("empty-list")}</td>
            </tr>}
            </tbody>
        </Table>
    )
}

const CoworkerView = (props) => {
    const {lang, index, botName} = props
    const [user, setUser] = useState({})
    const [activeTab] = useState('profile')
    const {t} = useTranslation()

    useEffect(() => {
        fetch(`${Helper.url}/api/user/info?id=${index}`, {
            headers: new Headers({
                Authorization: `Bearer ${window.token}`
            }),
            method: "GET",
        })
            .then(response => response.json())
            .then(data => {
                console.log(data)
                setUser(data)
            })
            .catch(error => console.error(error))
    }, [index]);
    useEffect(() => {
        i18next.changeLanguage(lang).then()
    }, [lang]);

    return (
        <Tab.Container defaultActiveKey={activeTab}>
            <Row>
                <Col sm={2}>
                    <Nav variant="pills" className="flex-column">
                        <Nav.Item>
                            <Nav.Link eventKey="profile">{t('profile')}</Nav.Link>
                        </Nav.Item>
                        <Nav.Item>
                            <Nav.Link eventKey="account">{t('account')}</Nav.Link>
                        </Nav.Item>
                        <Nav.Item>
                            <Nav.Link eventKey="advanced">{t('advanced')}</Nav.Link>
                        </Nav.Item>
                    </Nav>
                </Col>
                <Col sm={10}>
                    <Tab.Content>
                        <Tab.Pane eventKey="profile">
                            <ProfilePane model={user.profile} botName={botName}/>
                        </Tab.Pane>
                        <Tab.Pane eventKey="account">
                            <AccountPane model={user}/>
                        </Tab.Pane>
                        <Tab.Pane eventKey="advanced">
                            <AdvancedPane model={user}/>
                        </Tab.Pane>
                    </Tab.Content>
                </Col>
            </Row>
        </Tab.Container>
    )
}

export default CoworkerView