import {createContext, useContext, useState} from "react";
import {Helper} from "../constants/Helper";


// @ts-ignore
export const CalendarContext = createContext();

export const CalendarProvider = ({children}) => {
    const [date, setDate] = useState(new Date())
    const [month, setMonth] = useState(date.getMonth())
    const [year, setYear] = useState(date.getFullYear())
    const [coworkerId, setCoworkerId] = useState(null)
    const [lang, setLang] = useState(null)

    const getPrice = (user_id, date) =>
        fetch(`${Helper.url}/api/price/by-user?user_id=${user_id}&date=${date}`, Helper.FETCH_CONFIG)
            .then(response => response.json());
    const getHours = (year, month) =>
        fetch(`${Helper.url}/api/coworker/calendar?year=${year}&month=${month + 1}`, {
            ...Helper.FETCH_CONFIG,
            method: "GET"
        })
            .then(response => response.json());
    const getOrders = (user_id) =>
        fetch(`${Helper.url}/api/order/my?user_id=${user_id}`, Helper.FETCH_CONFIG)
            .then(response => response.json());
    const setHours = (data) => {
        // console.log(data)
        return fetch(`${Helper.url}/api/order/set-hours`, {
            ...Helper.FETCH_CONFIG,
            method: 'POST',
            body: Helper.buildForm(data)
        })
    };
    const findByDate = (array, date) => array.filter(e => e.date === date);

    const prevMonth = () => {
        const nextDate = new Date();
        if (month > 0) {
            setMonth(month - 1)
            nextDate.setMonth(month - 1)
        } else {
            setMonth(11)
            setYear(year - 1)
            nextDate.setMonth(11)
            nextDate.setFullYear(year - 1)
        }
        setDate(nextDate)
    }
    const nextMonth = () => {
        const nextDate = new Date();
        if (month > 10) {
            setMonth(0)
            setYear(year + 1);
            nextDate.setMonth(0)
            nextDate.setFullYear(year + 1)
        } else {
            setMonth(month + 1)
            nextDate.setMonth(month + 1)
        }
        setDate(nextDate)
    }
    return (
        <CalendarContext.Provider value={{
            date: date,
            month: month,
            year: year,
            setMonth: setMonth,
            setYear: setYear,
            getPrice: getPrice,
            getHours: getHours,
            getOrders: getOrders,
            setHours: setHours,
            findByDate: findByDate,
            nextMonth: nextMonth,
            prevMonth: prevMonth,
            language: lang,
            setLanguage: setLang,
            coworker: {
                id: coworkerId,
                setId: setCoworkerId
            },
        }}>
            {children}
        </CalendarContext.Provider>
    )
}

export const useCalendarContext = () => useContext(CalendarContext)