import {useEffect, useState} from "react";
import {v4 as uuid} from 'uuid';
import {useTranslation} from "react-i18next";
import i18next from "../../constants/Locale"
import "./BotFlowBuilder.css"

const BotFlowBuilder = (props) => {
    const {lang} = props;
    const [flows, setFlows] = useState([]);
    const [currentFlow, setCurrentFlow] = useState(null);
    const [selectedNode, setSelectedNode] = useState(null);
    const {t} = useTranslation()

    // Создание новой цепочки
    const createNewFlow = () => {
        const newNode = {
            id: uuid(),
            type: "message",
            text: "Добро пожаловать!",
            buttons: [],
            variables: {},
            next: null
        };

        const newFlow = {
            id: uuid(),
            name: "Новая цепочка",
            trigger: "command",
            triggerValue: "/start",
            nodes: [newNode]
        };

        setFlows([...flows, newFlow]);
        setCurrentFlow(newFlow);
        setSelectedNode(newNode);
    };

    // Сохранение цепочек в localStorage
    const saveFlows = () => {
        localStorage.setItem('botFlows', JSON.stringify(flows));
        alert('Цепочки сохранены!');
    };

    // Загрузка цепочек при монтировании
    useEffect(() => {
        const savedFlows = localStorage.getItem('botFlows');
        if (savedFlows) {
            const parsedFlows = JSON.parse(savedFlows);
            setFlows(parsedFlows);
            if (parsedFlows.length > 0) {
                setCurrentFlow(parsedFlows[0]);
                setSelectedNode(parsedFlows[0].nodes[0]);
            }
        }
    }, []);
    useEffect(() => {
        i18next.changeLanguage(lang)
    }, [lang]);

    // Обновление текущего потока
    const updateCurrentFlow = (updates) => {
        const updatedFlow = {...currentFlow, ...updates};
        setCurrentFlow(updatedFlow);

        setFlows(flows.map(flow =>
            flow.id === updatedFlow.id ? updatedFlow : flow
        ));
    };

    // Обновление узла в текущем потоке
    const updateNodeInFlow = (updatedNode) => {
        const updatedNodes = currentFlow.nodes.map(node =>
            node.id === updatedNode.id ? updatedNode : node
        );

        updateCurrentFlow({nodes: updatedNodes});

        if (selectedNode && selectedNode.id === updatedNode.id) {
            setSelectedNode(updatedNode);
        }
    };

    // Добавление нового узла
    const addNewNode = () => {
        const newNode = {
            id: uuid(),
            type: "message",
            text: "Новое сообщение",
            buttons: [],
            variables: {},
            next: null
        };

        updateCurrentFlow({
            nodes: [...currentFlow.nodes, newNode]
        });
        setSelectedNode(newNode);
    };

    // Удаление узла
    const deleteNode = (nodeId) => {
        const updatedNodes = currentFlow.nodes.filter(node => node.id !== nodeId);
        updateCurrentFlow({nodes: updatedNodes});

        if (selectedNode && selectedNode.id === nodeId) {
            setSelectedNode(updatedNodes.length > 0 ? updatedNodes[0] : null);
        }
    };

    return (
        <div className="container mx-auto p-4">
            <div className="row">
                {/* Список цепочек */}
                <div className="col-3 bg-gray-100 p-4 rounded-lg">
                    <div className="flex flex-col gap-2 mb-4">
                        <button onClick={createNewFlow} className="btn btn-success w-50">
                            + {t("botFlow.new-chain")}
                        </button>
                        <button onClick={saveFlows} className="btn btn-primary w-50">
                            {t("botFlow.save-all")}
                        </button>
                    </div>

                    <div className="space-y-2">
                        {flows.map(flow => (
                            <div key={flow.id}
                                 className={`p-3 rounded cursor-pointer ${
                                     currentFlow?.id === flow.id
                                         ? 'bg-primary text-white'
                                         : 'bg-default'
                                 }`}
                                 onClick={() => {
                                     setCurrentFlow(flow);
                                     setSelectedNode(flow.nodes[0]);
                                 }}
                            >
                                <div className="font-bold">{flow.name}</div>
                                <div className="text-sm">{flow.triggerValue}</div>
                            </div>
                        ))}
                    </div>
                </div>

                {/* Редактор */}
                {currentFlow && (
                    <div className="col-9">
                        <div className="bg-white p-4 rounded-lg shadow mb-4">
                            <FlowTriggerEditor
                                flow={currentFlow}
                                updateFlow={updateCurrentFlow}
                                lang={lang}
                            />
                        </div>
                        <div className="row">
                            {/* Визуализация цепочки */}
                            <div className="col-6 bg-gray-50 p-4 rounded-lg">
                                <h3 className="text-lg font-bold mb-3">Схема цепочки</h3>
                                <div className="space-y-3">
                                    {currentFlow.nodes.map(node => (
                                        <div
                                            key={node.id}
                                            className={`p-3 border rounded-lg cursor-pointer ${
                                                selectedNode?.id === node.id
                                                    ? 'border-blue-500 bg-blue-50'
                                                    : 'border-gray-300 hover:bg-gray-100'
                                            }`}
                                            onClick={() => setSelectedNode(node)}
                                        >
                                            <div className="flex justify-between items-start">
                                                <div>
                                                    <div className="font-bold text-sm capitalize">
                                                        {node.type === 'message' && 'Сообщение'}
                                                        {node.type === 'editMessage' && 'Редактирование'}
                                                        {node.type === 'answerCallback' && 'Callback ответ'}
                                                    </div>
                                                    <div className="text-sm mt-1 text-gray-600">
                                                        {node.text.substring(0, 30)}{node.text.length > 30 ? '...' : ''}
                                                    </div>
                                                </div>
                                                {node.buttons.length > 0 && (
                                                    <span
                                                        className="bg-blue-100 text-blue-800 text-xs px-2 py-1 rounded-full">
                            {node.buttons.length} кнопок
                          </span>
                                                )}
                                            </div>

                                            <button
                                                onClick={(e) => {
                                                    e.stopPropagation();
                                                    deleteNode(node.id);
                                                }} className="btn btn-danger"
                                            >
                                                <i className={"fas fa-trash"}></i> Удалить шаг
                                            </button>
                                        </div>
                                    ))}

                                    <button
                                        onClick={addNewNode}
                                        className="btn btn-outline-primary"
                                    >
                                        + {t("botFlow.add-step")}
                                    </button>
                                </div>
                            </div>

                            {/* Редактор узла */}
                            {selectedNode && (
                                <div className="col-6 bg-white p-4 rounded-lg shadow">
                                    <NodeEditor
                                        node={selectedNode}
                                        flow={currentFlow}
                                        onUpdate={updateNodeInFlow}
                                    />
                                </div>
                            )}
                        </div>
                    </div>
                )}
            </div>
        </div>
    );
};


// Редактор триггера цепочки
const FlowTriggerEditor = ({flow, updateFlow, lang}) => {
    const {t} = useTranslation()
    useEffect(() => {
        i18next.changeLanguage(lang)
    }, [lang]);
    return (
        <div>
            <h2 className="text-xl font-bold mb-4">{t("botFlow.chain-trigger")}</h2>

            <div className="grid grid-cols-2 gap-4 mb-4">
                <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">
                        {t("botFlow.trigger-type")}
                    </label>
                    <select
                        value={flow.trigger}
                        onChange={(e) => updateFlow({trigger: e.target.value})}
                        className="w-full p-2 border rounded"
                    >
                        <option value="command">Команда (например, /start)</option>
                        <option value="callback">Callback кнопка</option>
                        <option value="message">Текст сообщения</option>
                    </select>
                </div>

                <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">
                        Значение триггера
                    </label>
                    <input
                        type="text"
                        value={flow.triggerValue}
                        onChange={(e) => updateFlow({triggerValue: e.target.value})}
                        placeholder={flow.trigger === 'command' ? '/start' : 'Текст триггера'}
                        className="w-full p-2 border rounded"
                    />
                </div>
            </div>

            <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                    Название цепочки
                </label>
                <input
                    type="text"
                    value={flow.name}
                    onChange={(e) => updateFlow({name: e.target.value})}
                    placeholder="Название цепочки"
                    className="w-full p-2 border rounded"
                />
            </div>
        </div>
    );
};

// Редактор узла
const NodeEditor = ({node, flow, onUpdate}) => {
    const updateNode = (updates) => {
        onUpdate({...node, ...updates});
    };

    // Добавление кнопки
    const addButton = () => {
        const newButton = {
            id: uuid(),
            text: "Новая кнопка",
            action: "message",
            target: null,
            url: null
        };

        updateNode({buttons: [...node.buttons, newButton]});
    };

    // Обновление кнопки
    const updateButton = (buttonId, updates) => {
        const updatedButtons = node.buttons.map(btn =>
            btn.id === buttonId ? {...btn, ...updates} : btn
        );

        updateNode({buttons: updatedButtons});
    };

    // Удаление кнопки
    const deleteButton = (buttonId) => {
        updateNode({
            buttons: node.buttons.filter(btn => btn.id !== buttonId)
        });
    };

    return (
        <div>
            <h2 className="text-xl font-bold mb-4">Редактирование шага</h2>

            <div className="mb-4">
                <label className="block text-sm font-medium text-gray-700 mb-1">
                    Тип шага
                </label>
                <select
                    value={node.type}
                    onChange={(e) => updateNode({type: e.target.value})}
                    className="w-full p-2 border rounded"
                >
                    <option value="message">Сообщение</option>
                    <option value="editMessage">Редактирование сообщения</option>
                    <option value="answerCallback">Ответ на callback</option>
                </select>
            </div>

            <div className="mb-4">
                <label className="block text-sm font-medium text-gray-700 mb-1">
                    Текст сообщения
                </label>
                <textarea
                    value={node.text}
                    onChange={(e) => updateNode({text: e.target.value})}
                    rows={4}
                    className="w-full p-2 border rounded"
                />
            </div>

            <div className="mb-4">
                <div className="flex justify-between items-center mb-2">
                    <label className="block text-sm font-medium text-gray-700">
                        Кнопки
                    </label>
                    <button
                        onClick={addButton}
                        className="btn btn-primary"
                    >
                        + Добавить кнопку
                    </button>
                </div>

                <div className="space-y-3">
                    {node.buttons.map(button => (
                        <ButtonEditor
                            key={button.id}
                            button={button}
                            flow={flow}
                            onUpdate={updateButton}
                            onDelete={deleteButton}
                        />
                    ))}
                </div>
            </div>

            <div className="mb-4">
                <label className="block text-sm font-medium text-gray-700 mb-1">
                    Следующий шаг
                </label>
                <select
                    value={node.next || ''}
                    onChange={(e) => updateNode({next: e.target.value || null})}
                    className="w-full p-2 border rounded"
                >
                    <option value="">Завершить цепочку</option>
                    {flow.nodes
                        .filter(n => n.id !== node.id)
                        .map(n => (
                            <option key={n.id} value={n.id}>
                                Шаг: {n.text.substring(0, 30)}...
                            </option>
                        ))}
                </select>
            </div>
        </div>
    );
};

// Редактор кнопок
const ButtonEditor = ({button, flow, onUpdate, onDelete}) => {
    return (
        <div className="border p-3 rounded-lg bg-gray-50">
            <div className="grid grid-cols-2 gap-3 mb-3">
                <div>
                    <label className="block text-sm text-gray-600 mb-1">
                        Текст кнопки
                    </label>
                    <input
                        type="text"
                        value={button.text}
                        onChange={(e) => onUpdate(button.id, {text: e.target.value})}
                        placeholder="Текст кнопки"
                        className="w-full p-2 border rounded text-sm"
                    />
                </div>

                <div>
                    <label className="block text-sm text-gray-600 mb-1">
                        Действие
                    </label>
                    <select
                        value={button.action}
                        onChange={(e) => onUpdate(button.id, {action: e.target.value})}
                        className="w-full p-2 border rounded text-sm"
                    >
                        <option value="message">Отправить сообщение</option>
                        <option value="callback">Callback действие</option>
                        <option value="flow">Перейти к шагу</option>
                        <option value="url">URL ссылка</option>
                    </select>
                </div>
            </div>

            {button.action === 'flow' && (
                <div className="mb-3">
                    <label className="block text-sm text-gray-600 mb-1">
                        Выберите целевой шаг
                    </label>
                    <select
                        value={button.target || ''}
                        onChange={(e) => onUpdate(button.id, {target: e.target.value || null})}
                        className="w-full p-2 border rounded text-sm"
                    >
                        <option value="">Выберите шаг</option>
                        {flow.nodes.map(n => (
                            <option key={n.id} value={n.id}>
                                Шаг: {n.text.substring(0, 30)}...
                            </option>
                        ))}
                    </select>
                </div>
            )}

            {button.action === 'url' && (
                <div className="mb-3">
                    <label className="block text-sm text-gray-600 mb-1">
                        URL ссылка
                    </label>
                    <input
                        type="text"
                        value={button.url || ''}
                        onChange={(e) => onUpdate(button.id, {url: e.target.value})}
                        placeholder="https://example.com"
                        className="w-full p-2 border rounded text-sm"
                    />
                </div>
            )}

            {button.action === 'message' && (
                <div className="mb-3">
                    <label className="block text-sm text-gray-600 mb-1">
                        Текст сообщения
                    </label>
                    <textarea
                        value={button.target || ''}
                        onChange={(e) => onUpdate(button.id, {target: e.target.value})}
                        placeholder="Текст сообщения"
                        rows={2}
                        className="w-full p-2 border rounded text-sm"
                    />
                </div>
            )}

            {button.action === 'callback' && (
                <div className="mb-3">
                    <label className="block text-sm text-gray-600 mb-1">
                        Callback data
                    </label>
                    <input
                        type="text"
                        value={button.target || ''}
                        onChange={(e) => onUpdate(button.id, {target: e.target.value})}
                        placeholder="callback_data"
                        className="w-full p-2 border rounded text-sm"
                    />
                </div>
            )}

            <div className="flex justify-end">
                <button
                    onClick={() => onDelete(button.id)}
                    className="btn btn-danger"
                >
                    <i className={"fas fa-trash"}></i> Удалить кнопку
                </button>
            </div>
        </div>
    );
};

export default BotFlowBuilder;