import {useEffect, useState} from "react";
import {Helper} from "../../constants/Helper";
import {Button, Col, Form, Modal, Nav, Row, Tab, Table} from "react-bootstrap";
import {useForm} from "react-hook-form"
import {PatternFormat} from "react-number-format";
import {useTranslation} from "react-i18next";
import i18next from "../../constants/Locale"
import {useLoaderContext} from "../../hooks/LoaderContext";
import {useCoworkerContext} from "../../hooks/useCoworkerForm";

const ChangePassword = ({handleConfirm}) => {
    const [visible, setVisible] = useState(false)
    const {t} = useTranslation()
    const {setMessage} = useLoaderContext()
    const {register, reset, handleSubmit, watch, formState: {errors}} = useForm({
        mode: "onBlur",
        defaultValues: {
            old_password: "",
            new_password: "",
            confirm_password: "",
        }
    })
    const handleHide = () => {
        setVisible(false)
    }
    const handleShow = () => {
        setVisible(true)
    }
    const handleFormSubmit = (data) => {
        handleConfirm(data)
            .then(response => {
                if (response.ok) {
                    handleHide()
                    reset();
                    setMessage({type: "success", text: response.message})
                } else {
                    setMessage({type: "danger", text: response.message})
                }
            })
            .catch(error => setMessage({type: "danger", text: error.message}))
    }
    const rules = {
        required: {
            value: true,
            message: t("required_old_password")
        },
        minLength: {
            value: 4,
            message: t("minLength", {count: 4})
        }
    }
    return (
        <>
            <Modal show={visible} onHide={handleHide} size={"lg"}>
                <Modal.Header closeButton={true}>
                    <Modal.Title>Сменить пароль</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    {/* Old Password */}
                    <Form.Group>
                        <Form.Label column={true}>{t("old_password")}</Form.Label>
                        <Form.Control type={"password"} isValid={!errors.old_password} isInvalid={errors.old_password}
                                      placeholder={t("input_old_password")} {...register("old_password", rules)} />
                        <Form.Control.Feedback
                            type={errors.old_password ? "invalid" : "valid"}>{errors.old_password?.message}</Form.Control.Feedback>
                    </Form.Group>
                    {/* New Password */}
                    <Form.Group>
                        <Form.Label column={true}>{t("new_password")}</Form.Label>
                        <Form.Control type={"password"} isValid={!errors.new_password} isInvalid={errors.new_password}
                                      placeholder={t("input_new_password")} {...register("new_password", {
                            required: {
                                value: true,
                                message: t("required_field")
                            },
                            minLength: {
                                value: 4,
                                message: t("minLength", {count: 4})
                            },
                        })} />
                        <Form.Control.Feedback
                            type={errors.new_password ? "invalid" : "valid"}>{errors.new_password?.message}</Form.Control.Feedback>
                    </Form.Group>
                    {/* Confirm Password */}
                    <Form.Group>
                        <Form.Label column={true}>{t("confirm_password")}</Form.Label>
                        <Form.Control type={"password"} isValid={!errors.confirm_password}
                                      isInvalid={errors.confirm_password}
                                      placeholder={t("confirm_password")} {...register("confirm_password", {
                            required: {
                                value: true,
                                message: t("required_field")
                            },
                            minLength: {
                                value: 4,
                                message: t("minLength", {count: 4})
                            },
                            validate: (value) => {
                                return value !== watch("new_password") ? t("not_match_passwords") : null;
                            },
                        })} />
                        <Form.Control.Feedback
                            type={errors.confirm_password ? "invalid" : "valid"}>{errors.confirm_password?.message}</Form.Control.Feedback>
                    </Form.Group>
                </Modal.Body>
                <Modal.Footer>
                    <Row className={"w-100 d-flex justify-content-between"}>
                        <Button className={"w-25"} variant={"danger"} onClick={handleHide}>{t("cancel")}</Button>
                        <Button className={"w-25"} variant={"success"}
                                onClick={handleSubmit(handleFormSubmit)}>{t("apply")}</Button>
                    </Row>
                </Modal.Footer>
            </Modal>
            <Button variant={"outline-primary"} className={"w-100"} onClick={handleShow}>{t("change_password")}</Button>
        </>
    )
}

const CheckPassword = ({handleConfirm, id}) => {
    const {t} = useTranslation()
    const [visible, setVisible] = useState(false)
    const {setLoader, setMessage} = useLoaderContext()
    const {register, reset, handleSubmit, formState: {errors}} = useForm({
        defaultValues: {
            password: ""
        }
    })

    const handleHide = () => {
        setVisible(false)
    }
    const handleShow = () => {
        setVisible(true)
    }
    const handleFormSubmit = (data) => {
        setLoader(true)
        fetch(`${Helper.url}/api/user/check-password?id=${id}`, {
            headers: new Headers({
                Authorization: `Bearer ${window.token}`
            }),
            credentials: "include",
            method: "POST",
            body: Helper.buildForm({
                password: data.password
            })
        })
            .then(response => response.json())
            .then(response => {
                if (response.ok) {
                    handleConfirm(data);
                    reset()
                    handleHide()
                } else {
                    setMessage({type: "danger", text: response.message})
                }
            })
            .catch(error => setMessage({type: "danger", message: error.message}))
            .finally(() => setLoader(false))
    }

    return (
        <>
            <Modal show={visible} onHide={handleHide}>
                <Modal.Header closeButton={true}>
                    <Modal.Title>{t("check_password")}</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Form.Group>
                        <Form.Label column={true}>{t("password")}</Form.Label>
                        <Form.Control type={"password"} placeholder={t("input_password")} isValid={!errors.password}
                                      isInvalid={errors.password} {...register("password", {
                            required: {
                                value: true,
                                message: t("required_field")
                            },
                            minLength: {
                                value: 4,
                                message: t("minLength", {count: 4})
                            }
                        })} />
                        <Form.Control.Feedback
                            type={errors.password ? "invalid" : "valid"}>{errors.password?.message}</Form.Control.Feedback>
                    </Form.Group>
                </Modal.Body>
                <Modal.Footer>
                    <Row className={"w-100 d-flex justify-content-between"}>
                        <Button className={"w-25"} variant={"danger"} onClick={handleHide}>{t("cancel")}</Button>
                        <Button className={"w-25"} variant={"success"}
                                onClick={handleSubmit(handleFormSubmit)}>{t("apply")}</Button>
                    </Row>
                </Modal.Footer>
            </Modal>
            <Button variant={"success"} className={"w-100"} onClick={handleShow}>{t("apply")}</Button>
        </>
    )
}

const AddProperty = ({handleConfirm, title, categories, properties, selected, show}) => {
    const [visible, setVisible] = useState(show)
    const {t} = useTranslation()
    const [selectedCategory, setSelectedCategory] = useState(selected?.category)
    const [selectedProperty, setSelectedProperty] = useState(selected?.property)
    const [selectedDimension, setSelectedDimension] = useState(selected?.dimension)
    const [value, setValue] = useState(selected?.value)

    const {register, handleSubmit, formState: {errors}} = useForm({
        mode: "onBlur",
        defaultValues: {
            old_password: "",
            new_password: "",
            confirm_password: "",
        }
    })
    const handleHide = () => setVisible(false)
    const handleShow = () => {
        setSelectedCategory(categories[0])
        setSelectedProperty(categories[0]?.properties[0])
        setSelectedDimension(categories[0]?.properties[0]?.dimensions[0]);
        setVisible(true)
    };
    const isPropertyIsset = (data) => {
        return properties.filter(item => {
            return (
                item.property.id === data.property.id &&
                item.dimension.id === data.dimension.id &&
                item.category.id === data.category.id
            )
        })
    }
    const handleFormSubmit = () => {
        const item = {
            category: selectedCategory,
            property: selectedProperty,
            dimension: selectedDimension,
            value: value
        };
        if (isPropertyIsset(item)) {
            handleConfirm(item)
            handleHide()
        }
    }

    useEffect(() => {
        setSelectedCategory(selected?.category)
        setSelectedProperty(selected?.property)
        setSelectedDimension(selected?.dimension)
        setValue(selected?.value)
        setVisible(show);
    }, [show, selected]);

    const rules = {};

    return (
        <>
            <Modal show={visible} onHide={handleHide} size={"lg"}>
                <Modal.Header closeButton={true}>
                    <Modal.Title>{title}</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    {/* Category */}
                    <Form.Group className={"mb-3"}>
                        <Form.Label column={true}>{t("category")}</Form.Label>
                        <Form.Control as={"select"} isValid={!errors.category_id} isInvalid={errors.category_id}
                                      {...register("category_id", rules)}
                                      value={selected?.category.id ?? selectedCategory?.id}
                                      onChange={(event) => {
                                          const category = Helper.findById(categories, Number.parseInt(event.target.value));
                                          setSelectedCategory(category)
                                          setSelectedProperty(category.properties[0])
                                          setSelectedDimension(category.properties[0]?.dimensions[0])
                                      }}>
                            {categories.map(category => (
                                <option value={category.id} key={`category-${category.id}`}>{category.title}</option>
                            ))}
                        </Form.Control>
                        <Form.Control.Feedback
                            type={errors.category_id ? "invalid" : "valid"}>{errors.category_id?.message}</Form.Control.Feedback>
                    </Form.Group>
                    {/* Property */}
                    <Form.Group className={"mb-3"}>
                        <Form.Label column={true}>{t("select-property")}</Form.Label>
                        <Form.Control as={"select"} isValid={!errors.property_id} isInvalid={errors.property_id}
                                      {...register("property_id", rules)} value={selectedProperty?.id}
                                      onChange={(event) => {
                                          const property = Helper.findById(selectedCategory.properties, Number.parseInt(event.target.value))
                                          setSelectedProperty(property)
                                      }}>
                            {selectedCategory?.properties.map(property => (
                                <option value={property.id} key={`category-${property.id}`}>{property.title}</option>
                            ))}
                        </Form.Control>
                        <Form.Control.Feedback
                            type={errors.category_id ? "invalid" : "valid"}>{errors.category_id?.message}</Form.Control.Feedback>
                    </Form.Group>
                    {/* Value */}
                    <Form.Group className={"mb-3"}>
                        <Form.Label column={true}>{t("value")}</Form.Label>
                        <Form.Control type={"number"} isValid={!errors.value} isInvalid={errors.value}
                                      {...register("value", rules)}
                                      onChange={(event) => {
                                          const value = Number.parseInt(event.target.value)
                                          setValue(value)
                                      }}/>
                        <Form.Control.Feedback
                            type={errors.value ? "invalid" : "valid"}>{errors.value?.message}</Form.Control.Feedback>
                    </Form.Group>
                    {/* Dimension */}
                    <Form.Group className={"mb-3"}>
                        <Form.Label column={true}>{t("select-dimension")}</Form.Label>
                        <Form.Control as={"select"} isValid={!errors.dimension_id} isInvalid={errors.dimension_id}
                                      {...register("dimension_id", rules)} value={selectedDimension?.id}
                                      onChange={(event) => {
                                          const property = Helper.findById(selectedCategory.properties, Number.parseInt(event.target.value))
                                          setSelectedProperty(property)
                                      }}>
                            <option value={null} disabled={true}>{t('select-property')}</option>
                            {selectedProperty?.dimensions.map(dimension => (
                                <option value={dimension.id} key={`category-${dimension.id}`}>{dimension.title}</option>
                            ))}
                        </Form.Control>
                        <Form.Control.Feedback
                            type={errors.dimension_id ? "invalid" : "valid"}>{errors.dimension_id?.message}</Form.Control.Feedback>
                    </Form.Group>
                </Modal.Body>
                <Modal.Footer>
                    <Row className={"w-100 d-flex justify-content-between"}>
                        <Button className={"w-25"} variant={"danger"} onClick={handleHide}>{t("cancel")}</Button>
                        <Button className={"w-25"} variant={"success"}
                                onClick={handleSubmit(handleFormSubmit)}>{t("apply")}</Button>
                    </Row>
                </Modal.Footer>
            </Modal>
            <Button variant={"outline-primary"} className={"w-25"} onClick={handleShow}>{title}</Button>
        </>
    )
}

const AccountPane = ({isNewRecord}) => {
    const {user, setUser, setIsNewRecord} = useCoworkerContext()
    const {register, handleSubmit, setValue, formState: {errors}} = useForm({
        mode: "onBlur",
        values: {
            username: "",
            email: "",
            status: null,
            password: "",
            confirm_password: "",
            role: "employee"
        }
    })
    const {t} = useTranslation()
    const {setLoader, setMessage} = useLoaderContext()
    const [roles, setRoles] = useState([])

    useEffect(() => {
        setValue(["username"], user?.username ?? "")
        setValue(["email"], user?.email ?? "")
        setValue(["status"], user?.status ?? "")
        setValue(["priority"], user?.priority)
    }, [user, setValue]);
    useEffect(() => {
        fetch(`${Helper.url}/api/user/get-roles`, {
            headers: new Headers({
                Authorization: `Bearer ${window.token}`
            }),
            credentials: "include"
        })
            .then(response => response.json())
            .then(result => {
                setRoles(Object.entries(result).map(item => {
                    return {
                        index: item[0],
                        name: t(item[0])
                    }
                }));
            })
            .catch(error => {
                console.error(error)
            })
    }, [])

    const onSubmit = (data) => {
        if (!isNewRecord) {
            fetch(`${Helper.url}/api/user/update-account?id=${user?.id}`, {
                headers: new Headers({
                    Authorization: `Bearer ${window.token}`
                }),
                credentials: "include",
                method: "POST",
                body: Helper.buildForm({
                    username: data.username,
                    email: data.email,
                    status: data.status,
                    role: data.role,
                    priority_level: data.priority
                })
            })
                .then(response => response.json())
                .then(response => {
                    if (response.ok) {
                        setMessage({type: "success", text: response.message})
                    } else {
                        setMessage({type: "danger", text: response.message})
                    }
                })
                .catch(error => setMessage({type: "danger", text: error.message}))
        } else {
            fetch(`${Helper.url}/api/user/create-account`, {
                headers: new Headers({
                    Authorization: `Bearer ${window.token}`
                }),
                credentials: "include",
                method: "POST",
                body: Helper.buildForm({
                    'username': data.username,
                    'email': data.email,
                    'status': data.status,
                    'password': data.password,
                    role: data.role,
                    priority_level: data.priority
                })
            })
                .then(response => response.json())
                .then(response => {
                    if (response.ok) {
                        setUser(response.model)
                        setIsNewRecord(false)
                        setMessage({type: "success", text: response.message})
                        document.getElementById("coworker-form").setAttribute("data-index", response.model.id);
                        document.getElementById("coworker-form").setAttribute("data-is_new", false);
                    } else {
                        setMessage({type: "danger", text: response.message})
                    }
                })
                .catch(error => setMessage({type: "danger", text: error.message}))
        }
    }
    const changePasswordRequest = async (data) => {
        setLoader(true)
        return fetch(`${Helper.url}/api/user/change-password?id=${user?.id}`, {
            headers: new Headers({
                Authorization: `Bearer ${window.token}`
            }),
            credentials: "include",
            method: "POST",
            body: Helper.buildForm({
                old_password: data.old_password,
                new_password: data.new_password
            })
        })
            .then(response => response.json())
            .then(response => {
                console.log(response)
                return response;
            })
            .catch(error => console.error(error))
            .finally(() => {
                setLoader(false)
            })
    }

    return (
        <>
            {/* Username */}
            <Form.Group>
                <Form.Label column={true}>{t("username")}</Form.Label>
                <Form.Control type={"text"} placeholder={t("input_username")} {...register('username', {
                    required: {
                        value: true,
                        message: t("required_field")
                    },
                    minLength: {
                        value: 4,
                        message: t("minLength", {count: 4}),
                    }
                })} isValid={!errors.username} isInvalid={errors.username}/>
                <Form.Control.Feedback
                    type={errors.username ? "invalid" : "valid"}>{errors.username?.message}</Form.Control.Feedback>
            </Form.Group>
            {/* Email */}
            <Form.Group>
                <Form.Label column={true}>{t("email")}</Form.Label>
                <Form.Control type={"email"} placeholder={t("input_email")} {...register('email', {
                    required: {
                        value: true,
                        message: t("required_field")
                    },
                    pattern: {
                        value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i,
                        message: t("wrong_email_format")
                    }
                })} isValid={!errors.email} isInvalid={errors.email}/>
                <Form.Control.Feedback
                    type={errors.email ? "invalid" : "valid"}>{errors.email?.message}</Form.Control.Feedback>
            </Form.Group>
            {/* Status */}
            <Form.Group>
                <Form.Label column={true}>{t("status")}</Form.Label>
                <Form.Control as={"select"} {...register('status')}>
                    {Helper.status.map((item, index) => (
                        <option value={index} key={`status-${index}`}>{item}</option>
                    ))}
                </Form.Control>
            </Form.Group>
            {/* Role */}
            <Form.Group>
                <Form.Label column={true}>{t("role")}</Form.Label>
                <Form.Control as={"select"} {...register('role')}>
                    {roles.map((item) => (
                        <option value={item.index} key={`role-${item.index}`}>{item.name}</option>
                    ))}
                </Form.Control>
            </Form.Group>
            {/* Priority */}
            <Form.Group>
                <Form.Label column={true}>{t("priority")}</Form.Label>
                <Form.Control as={"select"} {...register('priority')}>
                    {Helper.priorities.map((item, index) => (
                        <option value={index} key={`role-${index}`}>{item}</option>
                    ))}
                </Form.Control>
            </Form.Group>
            {isNewRecord && <div className={"my-3"}>
                <Form.Group>
                    <Form.Label column={true}>{t("password")}</Form.Label>
                    <Form.Control type={"password"} {...register('password')} />
                    <Form.Control.Feedback
                        type={errors.password ? "invalid" : "valid"}>{errors.password?.message}</Form.Control.Feedback>
                </Form.Group>
                <Form.Group>
                    <Form.Label column={true}>{t("confirm_password")}</Form.Label>
                    <Form.Control type={"password"} {...register('confirm_password')} />
                    <Form.Control.Feedback
                        type={errors.confirm_password ? "invalid" : "valid"}>{errors.confirm_password?.message}</Form.Control.Feedback>
                </Form.Group>
            </div>}
            <Row className={"my-3"}>
                <Col sm={2}>
                    <ChangePassword handleConfirm={changePasswordRequest}/>
                </Col>
                <Col sm={2}>
                    {isNewRecord ?
                        <Button variant={'success'} className={"w-100"}
                                onClick={handleSubmit(onSubmit)}>{t('apply')}</Button> :
                        <CheckPassword id={user?.id} handleConfirm={handleSubmit(onSubmit)}/>
                    }
                </Col>
            </Row>
        </>
    )
}

const ProfilePane = ({model, i18next}) => {
    const {t} = useTranslation()
    const {setLoader, setMessage} = useLoaderContext()
    const {user, isNewRecord} = useCoworkerContext()
    const {register, setValue, handleSubmit, formState: {errors}} = useForm({
        mode: "onBlur",
        defaultValues: {
            family: "",
            name: "",
            surname: "",
            birthday: "",
            phone: ""
        }
    })
    useEffect(() => {
        if (!isNewRecord) {
            setValue(["family"], user?.profile?.family)
            setValue(["name"], user?.profile?.name)
            setValue(["surname"], user?.profile?.surname)
            setValue(["birthday"], user?.profile?.birthday)
            setValue(["phone"], user?.profile?.phone?.slice(1))
        }
    }, [user, setValue]);
    const formHandler = (data) => {
        setLoader(true)
        if (isNewRecord) {
            fetch(`${Helper.url}/api/user/create-profile`, {
                headers: new Headers({
                    Authorization: `Bearer ${window.token}`,
                }),
                credentials: "include",
                method: "POST",
                body: Helper.buildForm({
                    'Profile[family]': data.family ?? "",
                    'Profile[name]': data.name ?? "",
                    'Profile[surname]': data.surname ?? "",
                    'Profile[birthday]': data.birthday ?? "",
                    'Profile[phone]': data.phone ? `7${data.phone}` : ""
                })
            })
                .then(response => response.json())
                .then(response => {
                    console.log(response)
                })
                .finally(() => setLoader(false))
        } else {
            fetch(`${Helper.url}/api/user/update-profile?id=${user?.id}`, {
                headers: new Headers({
                    Authorization: `Bearer ${window.token}`,
                }),
                credentials: "include",
                method: "POST",
                body: Helper.buildForm({
                    family: data.family ?? "",
                    name: data.name ?? "",
                    surname: data.surname ?? "",
                    birthday: data.birthday ?? "",
                    phone: data.phone ? `7${data.phone}` : ""
                })
            })
                .then(response => response.json())
                .then(response => {
                    if (response.ok) {
                        setMessage({type: "success", text: response.message})
                    } else {
                        setMessage({type: "danger", text: response.message})
                    }
                })
                .finally(() => setLoader(false))
        }
        console.log(data)
    }

    useEffect(() => {
        i18next.changeLanguage('ru')
    }, [i18next]);

    return (
        <Form>
            {/* Фамилия */}
            <Form.Group className={"mb-3"}>
                <Form.Label column={true}>{t("family")}</Form.Label>
                <Form.Control placeholder={t("input_family")} {...register('family', {
                    minLength: {
                        value: 3,
                        message: t('minLength', {count: 3})
                    }
                })} isValid={!errors.family} isInvalid={errors.family}/>
                <Form.Control.Feedback
                    type={errors.family ? "invalid" : "valid"}>{errors.family?.message}</Form.Control.Feedback>
            </Form.Group>
            {/* Имя */}
            <Form.Group className={"mb-3"}>
                <Form.Label column={true}>{t("name")}</Form.Label>
                <Form.Control placeholder={t("input_name")} {...register('name', {
                    minLength: {
                        value: 3,
                        message: t("minLength", {count: 3})
                    }
                })} isValid={!errors.name} isInvalid={errors.name}/>
                <Form.Control.Feedback
                    type={errors.name ? "invalid" : "valid"}>{errors.name?.message}</Form.Control.Feedback>
            </Form.Group>
            {/* Отчество */}
            <Form.Group className={"mb-3"}>
                <Form.Label column={true}>{t("surname")}</Form.Label>
                <Form.Control placeholder={t("input_surname")} {...register('surname', {
                    minLength: {
                        value: 3,
                        message: t("minLength", {count: 3})
                    }
                })} isValid={!errors.surname} isInvalid={errors.surname}/>
                <Form.Control.Feedback
                    type={errors.surname ? "invalid" : "valid"}>{errors.surname?.message}</Form.Control.Feedback>
            </Form.Group>
            {/* Дата рождения */}
            <Form.Group className={"mb-3"}>
                <Form.Label column={true}>{t("birthday")}</Form.Label>
                <Form.Control type={"date"} placeholder={t("input_birthday")} {...register('birthday', {
                    validate: (value) => {
                        const newDate = new Date(value);
                        const today = new Date(Date.now());
                        return (today.getFullYear() - newDate.getFullYear()) < 18 ?
                            t("minYear", {year: today.getFullYear() - 18}) : null
                    }
                })} isValid={!errors.birthday} isInvalid={errors.birthday}/>
                <Form.Control.Feedback
                    type={errors.birthday ? "invalid" : "valid"}>{errors.birthday?.message}</Form.Control.Feedback>
            </Form.Group>
            {/* Номер телефона */}
            <Form.Group className={"mb-3"}>
                <Form.Label column={true}>{t("phone")}</Form.Label>
                <Form.Control as={PatternFormat} format={"+7 (###) ###-##-##"} isValid={!errors.phone}
                              allowEmptyFormatting={true} mask={"_"} isInvalid={errors.phone}
                              placeholder={t("input_phone")} value={model?.phone?.slice(1)}
                              onValueChange={(values) => {
                                  setValue("phone", values.value)
                              }}
                              {...register('phone', {
                                  validate: (value) => {
                                      const newValue = String(value);
                                      if (newValue.length === 0) {
                                          return null
                                      }
                                      if (newValue.length !== 10) {
                                          return t("wrong_phone_format")
                                      }
                                  }
                              })}/>
                <Form.Control.Feedback
                    type={errors.phone ? "invalid" : "valid"}>{errors.phone?.message}</Form.Control.Feedback>
            </Form.Group>
            <Button variant={"success"} onClick={handleSubmit(formHandler)}>{t("save")}</Button>
        </Form>
    )
}

const AdvancedPane = ({model, i18next}) => {
    const {t} = useTranslation()
    const [categories, setCategories] = useState([])
    const [properties, setProperties] = useState(model?.userProperties ?? [])
    const [selectedProperty, setSelectedProperty] = useState(null)
    const [isVisible, setIsVisible] = useState(false)
    const [price, setPrice] = useState(model?.price)
    const {setLoader} = useLoaderContext()

    useEffect(() => {
        fetch(`${Helper.url}/api/category/index`, {
            headers: new Headers({
                Authorization: `Bearer ${window.token}`
            }),
            credentials: "include",
            method: "POST",
        })
            .then(response => response.json())
            .then(result => {
                setCategories(result.results)
            })
    }, []);
    useEffect(() => {
        setProperties(model?.userProperties)
        setPrice(model?.price)
        console.log(model)
    }, [model]);

    const handleSave = () => {
        const form = new FormData();
        properties.map((property, propertyIndex) => {
            form.append(`User[userProperties][${propertyIndex}][category_id]`, property.category.id)
            form.append(`User[userProperties][${propertyIndex}][property_id]`, property.property.id)
            form.append(`User[userProperties][${propertyIndex}][dimension_id]`, property.dimension.id)
            form.append(`User[userProperties][${propertyIndex}][user_id]`, model.id)
            form.append(`User[userProperties][${propertyIndex}][value]`, property.value)
            return property;
        })
        form.append('User[price]', price);
        fetch(`${Helper.url}/api/coworker/advanced?id=${model.id}`, {
            headers: new Headers({
                Authorization: `Bearer ${window.token}`
            }),
            credentials: "include",
            method: "POST",
            body: form
        })
            .then(response => response.json())
            .then(result => {
                console.log(result)
            })
            .finally(_ => setLoader(false))
    }
    const handleUpdateProperty = (index) => {
        const item = properties.filter((_, itemIndex) => itemIndex === index)
        setSelectedProperty(item[0])
        setIsVisible(true)
    }
    const handleRemoveProperty = (index) => {
        const newArray = properties.filter((_, i) => i !== index)
        setProperties(newArray)
    }

    return (
        <>
            <Form.Group className={"mb-3"}>
                <Form.Label column={true}>{t('price')}</Form.Label>
                <Form.Control defaultValue={price} onChange={(e) => setPrice(e.target.value)}/>
            </Form.Group>
            <Table striped={true} hover={true} align={'center'}>
                <thead>
                <tr>
                    <td>#</td>
                    <td className={'col-5'}>{t('category')}</td>
                    <td className={'col-5'}>{t('requirements')}</td>
                    <td className={'col-1 text-center'}>&nbsp;</td>
                </tr>
                </thead>
                <tbody>
                {
                    properties?.map((item, index) => {
                        console.log(item)
                        return (
                            <tr key={`row-${index}`}>
                                <td>{index + 1}</td>
                                <td>
                                    <p>{item.category.title}</p>
                                    <input type={'hidden'} value={item.category.id} name={'property'}/>
                                </td>
                                <td>
                                    <p className={"mb-0"}>{item.property.title} {item.value} {item.dimension.title}</p>
                                    <input type={'hidden'} value={item.property.id}
                                           name={`requirement[${index}]property_id`}/>
                                    <input type={'hidden'} value={item.value}
                                           name={`requirement[${index}]value`}/>
                                    <input type={'hidden'} value={item.dimension.id}
                                           name={`requirement[${index}]dimension_id`}/>
                                </td>
                                <td>
                                    <div className={"d-flex flex-row justify-content-between"}>
                                        <i className={"fas fa-pencil text-primary"}
                                           onClick={_ => handleUpdateProperty(index)}></i>
                                        <i className={"fas fa-eye text-primary"}></i>
                                        <i className={"fas fa-trash text-primary"}
                                           onClick={_ => handleRemoveProperty(index)}></i>
                                    </div>
                                </td>
                            </tr>
                        )
                    })
                }
                </tbody>

            </Table>
            <AddProperty title={t("add-property")} categories={categories} handleConfirm={(data) => {
                setProperties([...properties, data])
            }} properties={properties} selected={selectedProperty} show={isVisible}/>
            <Button variant={'success'} className={'mx-2 w-25'} onClick={handleSave}>{t('save')}</Button>
        </>
    );
}

const CoworkerForm = (props) => {
    const {index, lang, isNew} = props;
    const {t} = useTranslation()
    const {user, setUser, isNewRecord, setIsNewRecord, activeTab, setActiveTab} = useCoworkerContext()
    const {setLoader} = useLoaderContext()

    useEffect(() => {
        i18next.changeLanguage(`${lang}`);
        if (isNew !== "true") {
            setLoader(true)
            fetch(`${Helper.url}/api/user/info?id=${index}`, {
                headers: new Headers({
                    Authorization: `Bearer ${window.token}`
                }),
            })
                .then(response => response.json())
                .then(response => {
                    setLoader(false)
                    setUser(response)
                    document.getElementById("coworker-form").setAttribute("data-index", response.id);
                    document.getElementById("coworker-form").setAttribute("data-is_new", false);
                })
                .catch(error => console.error(error))
        }
    }, [lang, index, isNew]);
    useEffect(() => {
        setIsNewRecord(isNew === "true")
    }, [isNew]);
    return (
        <Tab.Container defaultActiveKey={activeTab}>
            <Row>
                <Col sm={2}>
                    <Nav variant="pills" className="flex-column">
                        <Nav.Item className={'profile-link'}>
                            <Nav.Link eventKey="profile">{t('profile')}</Nav.Link>
                        </Nav.Item>
                        <Nav.Item className={'account-link'}>
                            <Nav.Link eventKey="account">{t('account')}</Nav.Link>
                        </Nav.Item>
                        {!isNewRecord && <Nav.Item className={'advanced-link'}>
                            <Nav.Link eventKey="advanced">{t('advanced')}</Nav.Link>
                        </Nav.Item>}
                    </Nav>
                </Col>
                <Col sm={10}>
                    <Tab.Content>
                        <Tab.Pane eventKey="profile">
                            <ProfilePane model={user?.profile} i18next={i18next} isNewRecord={isNewRecord}/>
                        </Tab.Pane>
                        <Tab.Pane eventKey="account">
                            <AccountPane model={user} i18next={i18next} isNewRecord={isNewRecord}/>
                        </Tab.Pane>
                        <Tab.Pane eventKey="advanced">
                            <AdvancedPane model={user} i18next={i18next} isNewRecord={isNewRecord}/>
                        </Tab.Pane>
                    </Tab.Content>
                </Col>
            </Row>
        </Tab.Container>
    )
}

export default CoworkerForm