import React, {useContext, useState} from "react";


const CoworkerContext = React.createContext({})

const CoworkerProvider = ({children}) => {
    const [user, setUser] = useState(null)
    const [isNewRecord, setIsNewRecord] = useState(false)
    const [activeTab, setActiveTab] = useState("account")

    return (
        <CoworkerContext value={{
            user: user,
            setUser: setUser,
            isNewRecord: isNewRecord,
            setIsNewRecord: setIsNewRecord,
            activeTab: activeTab,
            setActiveTab: setActiveTab
        }}>
            {children}
        </CoworkerContext>
    )
}

const useCoworkerContext = () => useContext(CoworkerContext)

export {CoworkerProvider, useCoworkerContext}