import {useEffect, useState} from "react";
import {Button, Col, Form, Modal} from "react-bootstrap";
import {Helper} from "../../constants/Helper";
import "./DynamicTable.css"
import {useDynamicTableContext} from "../../hooks/DynamicTableContext";
import {useTranslation} from "react-i18next";
import i18next from "../../constants/Locale";
import {Controller, useForm} from "react-hook-form";
import Select from "react-select/base";
import {Input} from "./Input";

const defaultCurrent = {
    category_id: "", property_id: "", dimension_id: "", order_id: "", value: "", count: "", type: ""
};
const fetchConfig = {
    headers: new Headers({
        Authorization: 'Bearer ' + window.token,
        'Content-Type': 'application/json',
    }), method: "GET"
};
const getCategories = async () => {
    return fetch(`${Helper.url}/api/category/index`, fetchConfig)
        .then(response => response.json())
        .then(result => result.results)
}
const getOrder = async (index) => {
    return await fetch(`${Helper.url}/api/order/view?id=${index}`, fetchConfig)
        .then(response => response.json())
}
const getProperty = async (index) => {

}

const Table = ({data, update, remove}) => {
    const {dimensions, properties, categories} = useDynamicTableContext()
    return (<table className={"table table-striped"}>
        <tbody>
        {data?.map((item, index) => {
            const category = Helper.findById(categories, item?.category_id)
            const property = Helper.findById(properties, item?.property_id)
            const dimension = Helper.findById(dimensions, item?.dimension_id)
            const type = item?.type
            const value = item?.value
            return (<tr key={`row-${index}`}>
                <td className={"col-4"}>{category?.title}<input
                    name={`Order[requirements][${index}][category_id]`}
                    type={"hidden"}
                    value={category?.id}/></td>
                <td className={"col-2"}>{item.count}<input name={`Order[requirements][${index}][count]`}
                                                           type={"hidden"}
                                                           value={item.count}/>
                </td>
                <td className={"col-5"}>
                    <div className={"text-left"} key={`requirement-${index}`}>
                        {property?.title} {Helper.types[type]} {value} {dimension?.title}
                        <input name={`Order[requirements][${index}][property_id]`}
                               type={"hidden"} value={property?.id}/>
                        <input name={`Order[requirements][${index}][type]`}
                               type={"hidden"} value={type}/>
                        <input name={`Order[requirements][${index}][value]`}
                               type={"hidden"} value={value}/>
                        <input
                            name={`Order[requirements][${index}][dimension_id]`}
                            type={"hidden"} value={dimension?.id}/>
                    </div>
                </td>
                <td className={"col-1"}>
                        <span className={"text-primary"}> <i className={"fas fa-pencil"}
                                                             onClick={() => {
                                                                 console.log(index)
                                                                 update(index)
                                                             }}></i></span>
                    <span className={"text-primary"}> <i className={"fas fa-trash"}
                                                         onClick={() => remove(index)}></i></span>
                </td>
            </tr>)
        })}
        </tbody>
    </table>)
}

const MyModal = ({current, visibility, handleDismissModal, handleConfirmModal, categories}) => {
    const {t} = useTranslation()
    const [data, setData] = useState(current)
    const {register, control, setValue, trigger, reset, handleSubmit, watch, formState: {errors}} = useForm({
        defaultValue: {
            dimension_id: data.dimension_id ?? "",
            category_id: data.category_id ?? "",
            property_id: data.property_id ?? "",
            type: data.type ?? "",
            value: data.value ?? "",
            count: data.count ?? ""
        }, mode: "onChange"
    })
    const [selectedCategory, setSelectedCategory] = useState(null)
    const [selectedCategoryOption, setSelectedCategoryOption] = useState(null)
    const [selectedProperty, setSelectedProperty] = useState(null)
    const [selectedDimension, setSelectedDimension] = useState(null)
    const [options, setOptions] = useState([])
    const [isMenuOpen, setIsMenuOpen] = useState(false)
    const [searchText, setSearchText] = useState("")

    const handleClose = () => {
        reset()
        handleDismissModal()
    }
    const handleChange = (option, field) => {
        const category = Helper.findById(categories, option.value)
        setSelectedCategoryOption(option)
        field.onChange(category.id)
    }
    const handleOpenMenu = () => {
        console.log('open')
        setIsMenuOpen(true)
    }
    const handleCloseMenu = () => {
        console.log('close')
        setIsMenuOpen(false)
    }
    const handleFilter = (option, inputValue) => {
        return !option.label.search(inputValue)
    }
    const handleSearchTextChange = (value) => {
        setSearchText(value)
    }
    useEffect(() => {
        setSelectedCategory(Helper.findById(categories, data.category_id) ?? {})
        setValue('category_id', data.category_id)
    }, [data])
    useEffect(() => {
        setData(current)
    }, [current])
    useEffect(() => {
        setSelectedProperty(Helper.findById(selectedCategory?.properties ?? [], current.property_id))
        setValue('property_id', data.property_id)
        setValue('value', data.value)
        setValue('count', data.count)
        setValue('type', data.type)
    }, [selectedCategory])
    useEffect(() => {
        if (watch('category_id')) {
            setSelectedCategory(Helper.findById(categories, Number.parseInt(watch('category_id'))))
        }
        if (watch('property_id')) {
            setSelectedProperty(Helper.findById(selectedCategory.properties, Number.parseInt(watch('property_id'))))
        }
    }, [watch('category_id'), watch('property_id')])
    useEffect(() => {
        setSelectedDimension(Helper.findById(selectedProperty?.dimensions ?? [], current.dimension_id))
        setValue('dimension_id', data.dimension_id)
        trigger();
    }, [selectedProperty]);
    useEffect(() => {
        setOptions(categories?.map(item => {
            return {value: item.id, label: item.title}
        }))
        console.log(typeof errors.category_id)
    }, [categories]);

    return (<Modal show={visibility} onHide={handleClose} size={"lg"}>
        <Modal.Header backdrop={"static"}>
            <Modal.Title>{t('dynamic.add_property')}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
            <Form>
                {/* Current Category  */}
                <Input selected={selectedCategoryOption} options={options} placeholder={""}
                       name={"category_id"} control={control} errors={errors.category_id} label={t("dynamic.category")}
                       onChange={handleChange}/>
                {/*<Form.Group className={"mb-3"}>*/}
                {/*    <Form.Label column={true}>{t("dynamic.category")}</Form.Label>*/}
                {/*    <Controller render={({field}) => (*/}
                {/*        <Select {...field} value={selectedCategoryOption} options={options} menuIsOpen={isMenuOpen}*/}
                {/*                filterOption={handleFilter} onMenuOpen={handleOpenMenu} onMenuClose={handleCloseMenu}*/}
                {/*                onInputChange={handleSearchTextChange} onChange={(value) => handleChange(value, field)}*/}
                {/*                isSearchable={true} inputValue={searchText} placeholder={"Search or select"}/>*/}
                {/*    )} name={"category_id"} control={control}/>*/}
                    <Form.Control.Feedback
                        type={errors.category_id ? "invalid" : "valid"}>{errors.category_id?.message}</Form.Control.Feedback>
                {/*</Form.Group>*/}
                {/* Current Count     */}
                <Form.Group className={"mb-3"}>
                    <Form.Label column={true}>{t("dynamic.count")}</Form.Label>
                    <Form.Control type={"number"} defaultValue={current.count}
                                  disabled={watch("category_id") === ""}
                                  {...register("count", {
                                      required: {
                                          value: true, message: t("dynamic.required-field")
                                      }
                                  })}/>
                    <Form.Control.Feedback
                        type={errors.count ? "invalid" : "valid"}>{errors.count?.message}</Form.Control.Feedback>
                </Form.Group>
                {/* Current Requirements */}
                <div className={"row"}>
                    {/* Requirement Property  */}
                    <Form.Group as={Col}>
                        <Form.Label column={true}>{t("dynamic.property")}</Form.Label>
                        <Form.Select defaultValue={current.property_id} className={"form-control"}
                                     disabled={!(watch('category_id') !== "" && watch('count') !== "")}
                                     {...register("property_id", {
                                         required: {
                                             value: true, message: t("dynamic.required-field")
                                         }
                                     })}>
                            <option value={""}>{t("dynamic.select-property")}</option>
                            {selectedCategory?.properties?.map(property => {
                                return <option value={property.id}
                                               key={`requirement-property-${property.id}`}>{property.title}</option>
                            })}
                        </Form.Select>
                        <Form.Control.Feedback
                            type={errors.property_id ? "invalid" : "valid"}>{errors.property_id?.message}</Form.Control.Feedback>
                    </Form.Group>
                    {/* Requirement Type      */}
                    <Form.Group as={Col}>
                        <Form.Label column={true}>{t("dynamic.type")}</Form.Label>
                        <Form.Control as={"select"} defaultValue={current?.type}
                                      className={"form-control"} {...register("type", {
                            required: {
                                value: true, message: t("dynamic.required-field")
                            }
                        })} disabled={watch("property_id") === ""}>
                            <option value={""}>Сравнение</option>
                            {Object.entries(Helper.types).map(([key, value], typeIndex) => (<option value={key}
                                                                                                    key={`requirement-type-${typeIndex}`}>{value}</option>))}
                        </Form.Control>
                        <Form.Control.Feedback
                            type={errors.type ? "invalid" : "valid"}>{errors.type?.message}</Form.Control.Feedback>
                    </Form.Group>
                    {/* Requirement Value     */}
                    <Form.Group as={Col}>
                        <Form.Label column={true}>{t("dynamic.value")}</Form.Label>
                        <Form.Control type={"number"} defaultValue={current?.value} {...register("value", {
                            required: {
                                value: true, message: t("dynamic.required-field")
                            }
                        })} disabled={watch("type") === ""}/>
                        <Form.Control.Feedback
                            type={errors.value ? "invalid" : "valid"}>{errors.value?.message}</Form.Control.Feedback>
                    </Form.Group>
                    {/* Requirement Dimension */}
                    <Form.Group as={Col}>
                        <Form.Label column={true}>{t("dynamic.dimension")}</Form.Label>
                        <Form.Select defaultValue={current.dimension_id}
                                     className={"form-control"} {...register("dimension_id", {
                            required: {
                                value: true, message: t("dynamic.required-field")
                            }
                        })} disabled={watch("value") === ""}>
                            <option value={""}>{t("dynamic.select-dimension")}</option>
                            {selectedProperty?.dimensions?.map(dimension => (<option value={dimension.id}
                                                                                     key={`requirement-dimension-${dimension.id}`}>{dimension.title}</option>))}
                        </Form.Select>
                        <Form.Control.Feedback
                            type={errors.dimension_id ? "invalid" : "valid"}>{errors.dimension_id?.message}</Form.Control.Feedback>
                    </Form.Group>
                </div>
            </Form>
        </Modal.Body>
        <Modal.Footer>
            <Button variant={"danger"} onClick={handleClose}>Отмена</Button>
            <Button variant={"primary"} onClick={handleSubmit(handleConfirmModal)}>Сохранить</Button>
        </Modal.Footer>
    </Modal>)
}

export default function DynamicTable(props) {
    const {index, lang, isNew} = props
    const [dynamicTableModal, setDynamicTableModal] = useState(false)
    const [selectedIndex, setSelectedIndex] = useState(null)
    const [current, setCurrent] = useState(defaultCurrent)
    const [requirements, setRequirements] = useState([])
    const {categories} = useDynamicTableContext()
    const {t} = useTranslation()
    const handleModalClick = () => {
        setCurrent(defaultCurrent)
        setDynamicTableModal(true)
    }
    const handleConfirmModal = (data) => {
        if (selectedIndex !== null) {
            setRequirements(requirements.map((item, index) => index === selectedIndex ? {
                dimension_id: Number.parseInt(data.dimension_id),
                property_id: Number.parseInt(data.property_id),
                category_id: Number.parseInt(data.category_id),
                count: Number.parseInt(data.count),
                value: Number.parseInt(data.value),
                type: data.type
            } : item))
        } else {
            setRequirements([...requirements, {
                dimension_id: Number.parseInt(data.dimension_id),
                property_id: Number.parseInt(data.property_id),
                category_id: Number.parseInt(data.category_id),
                count: Number.parseInt(data.count),
                value: Number.parseInt(data.value),
                type: data.type
            }])
        }
        setSelectedIndex(null)
        setDynamicTableModal(false)
    }
    const handleDismissModal = () => {
        setCurrent(defaultCurrent)
        setSelectedIndex(null)
        setDynamicTableModal(false)
    }
    const handleUpdate = (value) => {
        setCurrent(requirements[Number.parseInt(value)])
        setSelectedIndex(Number.parseInt(value))
        setDynamicTableModal(true)
    }
    const handleRemove = (value) => {
        setRequirements(requirements.filter((item, index) => index !== value))
        // setOrder(order?.requirements.filter((item, index) => {
        //     return index !== value
        // }))
    }

    useEffect(() => {
        i18next.changeLanguage(lang)
        if (!(isNew === "true")) {
            Promise.all([getOrder(index), getCategories()])
                .then(([o, categories]) => {
                    setRequirements(o.requirements)
                })
        }
    }, [index, lang, isNew]);

    return (<div id={"my-table"} className={"container-fluid"}>
        <Button variant={"primary"} onClick={handleModalClick}
                className={"mb-3"}>{t("dynamic.add_property")}</Button>
        <Table data={requirements} update={handleUpdate} remove={handleRemove}/>
        <MyModal current={current} setCurrent={setCurrent} visibility={dynamicTableModal}
                 handleDismissModal={handleDismissModal} handleConfirmModal={handleConfirmModal}
                 categories={categories}/>
    </div>)
}