import React, {useCallback, useEffect, useState} from "react";
import {useCalendarContext} from "../../hooks/CalendarContext";
import {Helper} from "../../constants/Helper";
import {isBefore} from "date-fns";

const CalendarCell = React.memo(({index, element, currentDate, onClick, onAdd, isFinance}) => {
    const {month, year, findByDate, getPrice} = useCalendarContext();
    const dateStr = `${year}-${Helper.zeroPad(month + 1, 2)}-${Helper.zeroPad(index + 1, 2)}`;
    const day = findByDate(element.hours, dateStr);
    const newDate = new Date(year, month, index + 1);
    const [value, setValue] = useState(0)

    const isPast = isBefore(newDate, currentDate);

    const handleClick = useCallback(() => {
        if (day) {
            onClick(day);
        } else if (isPast) {
            getPrice(element.user.id, dateStr).then((result) => {
                onClick({"price":result.price,"user_id":result.user_id,"date":dateStr, "is_payed":false})
            });
        }
    }, [day, element, dateStr, isPast, onClick, getPrice]);

    const handleAdd = useCallback(() => {
        getPrice(element.user.id, dateStr).then((result) => {
            onAdd([{
                user_id: element.user.id,
                date: `${newDate.getFullYear()}-${Helper.zeroPad(newDate.getMonth() + 1, 2)}-${Helper.zeroPad(newDate.getDate(), 2)}`,
                price: result.price
            }])
        });
    }, [newDate, element])

    useEffect(() => {
        if (day.length) {
            const t = day?.reduce((total, item) => isFinance ? total + (item.count * item.price) : total + item.count, 0)
            setValue(t)
        }
    }, [isFinance, day]);

    if (!isPast) {
        return <td className="disable" />;
    }

    if (day.length) {
        return (
            <td key={`${element.user.id}-${dateStr}`} className={"free"} onClick={handleClick}>
                {value}
            </td>
        );
    }

    return (
        <td key={`${element.id}-${dateStr}`} className={"free un"} onClick={handleAdd}>
            0
        </td>
    );
});

export {CalendarCell}