import {createContext, useContext, useEffect, useState} from "react";
import {Helper} from "../constants/Helper";

declare global {
    // Note the capital "W"
    interface Window { token: string; }
}

const getCategories = async (token:string) => {
    return await fetch(`${Helper.url}/api/category/index`, {
        headers: new Headers({
            Authorization: `Bearer ${token}`
        }),
    })
        .then(response => response.json())
        .then(result => {
            // console.log(result.results)
            return result.results
        })
        .catch(error => console.error(error))
}
const getProperties = async (token:string) => {
    return await fetch(`${Helper.url}/api/property/index`, {
        headers: new Headers({
            Authorization: `Bearer ${token}`
        }),
    })
        .then(response => response.json())
        .then(response => response.ok ? response.data : [])
        .catch(error => console.error(error))
}
const getDimensions = async (token:string) => {
    return await fetch(`${Helper.url}/api/dimension/index`, {
        headers: new Headers({
            Authorization: `Bearer ${token}`
        }),
    })
        .then(response => response.json())
        .then(response => response.ok ? response.data : [])
        .catch(error => console.error(error))
}
export const DynamicTableContext = createContext({});

export const DynamicTableProvider = ({children}: {
    children: any
}) => {
    const [categories, setCategories] = useState([])
    const [properties, setProperties] = useState([])
    const [dimensions, setDimensions] = useState([])
    const [token, setToken] = useState()

    useEffect(() => {
        Promise.all([getCategories(window?.token), getProperties(window?.token), getDimensions(window?.token)])
            .then(([c, p, d]) => {
                setCategories(c);
                setProperties(p);
                setDimensions(d)
            })
            .catch(error => {
                console.error(error)
            })
    }, [token]);
    return (
        <DynamicTableContext.Provider value={{
            categories: categories,
            properties: properties,
            dimensions: dimensions,
            setToken: setToken,
        }}>
            {children}
        </DynamicTableContext.Provider>
    )
}

export const useDynamicTableContext = () => useContext(DynamicTableContext)