import {Form} from "react-bootstrap";
import {
    Control,
    Controller,
    ControllerRenderProps,
    FieldError,
    FieldErrorsImpl,
    Merge
} from "react-hook-form";
import Select from "react-select/base";
import {useState} from "react";
import {FilterOptionOption, OptionsOrGroups} from "react-select";

export const Input = ({selected, options, placeholder, name, control, errors, label, onChange}: {
    selected: any,
    options: OptionsOrGroups<any, any>,
    placeholder: string,
    name: string,
    control: Control<any, any, any>,
    errors: FieldError | Merge<FieldError, FieldErrorsImpl<any>> | undefined,
    label: string,
    onChange: (value: any, field: ControllerRenderProps<any, string>) => void
}) => {
    const [isMenuOpen, setIsMenuOpen] = useState(false)
    const [searchText, setSearchText] = useState("")
    const handleOpenMenu = () => setIsMenuOpen(true)
    const handleCloseMenu = () => setIsMenuOpen(false)
    const handleFilter = (option: FilterOptionOption<any>, inputValue: string) => {
        return option.label.toLowerCase().search(inputValue.toLowerCase()) > -1
    }
    const handleSearchTextChange = (value:string) => {
        setSearchText(value)
    }
    return (
        <Form.Group className={"mb-3"}>
            <Form.Label column={true}>{label}</Form.Label>
            <Controller render={({field}) => (
                <Select {...field} value={selected} options={options} menuIsOpen={isMenuOpen}
                        filterOption={handleFilter} onMenuOpen={handleOpenMenu} onMenuClose={handleCloseMenu}
                        onInputChange={handleSearchTextChange} onChange={(value) => onChange(value, field)}
                        isSearchable={true} inputValue={searchText} placeholder={placeholder}/>
            )} name={name} control={control}/>
            <Form.Control.Feedback
                type={errors ? "invalid" : "valid"}>{errors?.message?.toString()}</Form.Control.Feedback>
        </Form.Group>
    )
}