import {Button, Form, InputGroup} from "react-bootstrap";
import {useTranslation} from "react-i18next";
import {useEffect, useState} from "react";
import {Helper} from "../../constants/Helper";
import {Placemark, Map, YMaps, Circle} from "@pbe/react-yandex-maps";
import "./YandexMaps.css"
import i18next from "i18next";

const YandexMaps = (props) => {
    const {index, apiKey, suggestKey, lang, formName, radiusName} = props
    const {t} = useTranslation()
    const [map, setMap] = useState(null)
    const [center, setCenter] = useState([55.75, 37.57])
    const [markerCoords, setMarkerCoords] = useState([55.75, 37.57])
    const [address, _setAddress] = useState("")
    const [radius, setRadius] = useState(0)
    const [mapVisible, setMapVisible] = useState(false)

    const setAddress = (value) => {
        _setAddress(value)
        fetch(`https://geocode-maps.yandex.ru/v1/?apikey=${apiKey}&geocode=${value}&format=json`, {
            headers: new Headers({
                'Content-Type': "application/json"
            })
        })
            .then(response => response.json())
            .then(result => {
                const collection = result.response.GeoObjectCollection;
                if (collection) {
                    const geoObject = collection.featureMember[0].GeoObject
                    const position = geoObject.Point?.pos;
                    if (position) {
                        const a = position.split(' ')
                        const coords = [Number.parseFloat(a[1]), Number.parseFloat(a[0])]
                        setMarkerCoords(coords)
                        setCenter(coords)
                    }
                }
            })
    }
    const getAddress = async (coords) => {
        return map.geocode(coords)
            .then(result => {
                const firstGeoObject = result.geoObjects.get(0);
                const coords = firstGeoObject.geometry._coordinates;
                _setAddress(firstGeoObject.getAddressLine());
                return coords
            })
            .catch(error => {
                console.log(error)
            })
    }
    const getBuilding = async (id) => {
        return fetch(`${Helper.url}/api/building/view?id=${id}`, {
            headers: new Headers({
                Authorization: `Bearer ${window.token}`
            }),
            credentials: "include",
            mode: "cors",
        })
            .then(response => response.json())
    }

    useEffect(() => {
        if (index) {
            getBuilding(index)
                .then(result => {
                    setCenter([result?.location?.latitude, result?.location?.longitude])
                    setAddress(result?.location?.address)
                    setMarkerCoords([result?.location?.latitude, result?.location?.longitude])
                    setRadius(result?.radius)
                })
        } else {
            navigator.geolocation.getCurrentPosition((position) => {
                setCenter([position.coords.latitude, position.coords.longitude])
                setMarkerCoords([position.coords.latitude, position.coords.longitude])
            })
        }
    }, [index]);
    useEffect(() => {
        i18next.changeLanguage(lang)
    }, [lang])

    const handleToggleVisible = () => {
        setMapVisible(!mapVisible)
    }
    const handleMapClick = (event) => {
        setMarkerCoords(event.get('coords'))
        getAddress(event.get('coords'))
            .then(_ => {
                setCenter(event.get('coords'))
            })
    }
    const handleChangeAddress = (event) => {
        const {target} = event
        setAddress(target.value)
    }
    return (
        <YMaps query={{lang: lang, apikey: apiKey, suggest_apikey: suggestKey, load: ['geocode']}}>
            <Form.Group>
                <Form.Label column={false}>{t('address')}</Form.Label>
                <InputGroup className={"mb-3"}>
                    <Form.Control value={address} onChange={handleChangeAddress}
                                  name={`${formName}[address]`} className={mapVisible ? "disabled" : ""}/>
                    <Button variant={"outline-secondary"} onClick={handleToggleVisible}>
                        <i className={"fas fa-location-dot"}/>
                    </Button>
                </InputGroup>
                <input type={'hidden'} name={`${formName}[latitude]`} value={markerCoords[0]}/>
                <input type={'hidden'} name={`${formName}[longitude]`} value={markerCoords[1]}/>
            </Form.Group>
            {mapVisible &&
                <Map state={{center: center, zoom: 13}} height={400} width={"100%"} onClick={handleMapClick}
                     onLoad={(maps) => setMap(maps)} modules={['geocode']}>
                    <Placemark geometry={markerCoords}/>
                    <Circle geometry={[markerCoords, radius]} options={{
                        fillColor: "#DB709377",
                        strokeColor: "#990066",
                        strokeOpacity: 0.8,
                        strokeWidth: 2,
                    }}/>
                </Map>
            }
            <Form.Group>
                <Form.Label column={false}>{t('radius')}</Form.Label>
                <Form.Control value={radius} name={`${radiusName}[radius]`} onChange={event => {
                    if (event.target.value) {
                        setRadius(Number.parseInt(event.target.value))
                    }
                }}/>
            </Form.Group>
        </YMaps>
    )
}

const YandexMapsView = (props) => {
    const {index, apiKey, lang, height, width} = props
    const [model, setModel] = useState(null)
    const [center, setCenter] = useState([])

    useEffect(() => {
        fetch(`${Helper.url}/api/building/view?id=${index}`, {
            headers: new Headers({
                Authorization: `Bearer ${window.token}`
            }),
            credentials: "include"
        })
            .then(response => response.json())
            .then(result => {
                console.log(result)
                setModel(result)
                setCenter([result?.location?.latitude, result?.location?.longitude])
                console.log([result?.location?.latitude, result?.location?.longitude])
            })
            .catch(error => console.error(error))
    }, [index]);

    return (
        <YMaps query={{lang: lang, apikey: apiKey}}>
            <Map state={{center: center, zoom: 18}} height={height} width={width}>
                <Placemark geometry={center}/>
                <Circle geometry={[center, model?.radius]} options={{
                    fillColor: "#DB709377",
                    strokeColor: "#990066",
                    strokeOpacity: 0.8,
                    strokeWidth: 2,
                }}/>
            </Map>
        </YMaps>
    )
}

export {YandexMaps, YandexMapsView}