import {Button, Form, Modal} from "react-bootstrap";
import {useTranslation} from "react-i18next";
import {useEffect, useState} from "react";
import {Helper} from "../../constants/Helper";
import {useForm} from "react-hook-form";

export const AddModal = ({show, onClose, onSave, data}) => {
    const {t} = useTranslation()
    const [orderList, setOrderList] = useState([])
    const {reset, register, handleSubmit, setValue} = useForm({
        defaultValues: {
            order_id: 0,
            count: 0,
            date: "",
            is_payed: 0,
            user_id: 0
        }
    })
    const handleSave = (data) => {
        // console.log(data)
        fetch(`${Helper.url}/api/hours/quick`, {
            ...Helper.FETCH_CONFIG, ...{
                method: "POST",
                body: JSON.stringify(data)
            }
        })
            .then(response => response.json())
            .then(result => {
                if (result.ok) {
                    onClose()
                    window.location.reload()
                }
            })
            .finally(() => {
                reset()
            })
    }
    useEffect(() => {
        if (show && data.length) {
            setValue('date', data[0].date)
            setValue('user_id', data[0].user_id)
            fetch(`${Helper.url}/api/order/my?user_id=${data[0].user_id}&date=${data[0].date}`, Helper.FETCH_CONFIG)
                .then(response => response.json())
                .then(response => {
                    setOrderList(response)
                })
        }
    }, [show, data, setValue]);

    return (
        <Modal show={show} onHide={() => {
            reset()
            onClose()
        }} style={{zIndex: 9999}} size={'lg'} centered={true}>
            <Modal.Header closeButton={true}>{t('calendar.add')}</Modal.Header>
            <Modal.Body>
                <Form.Group>
                    <Form.Label column={true}>{t('calendar.order')}</Form.Label>
                    <Form.Control as={'select'} {...register('order_id')}>
                        {orderList?.map((item, index) => {
                            return (
                                <option key={`order-select-${index}`}
                                        value={item.id}>{t('calendar.order')} #{item.id}</option>
                            )
                        })}
                    </Form.Control>
                </Form.Group>
                <Form.Group>
                    <Form.Label column={true}>{t('calendar.quantity')}</Form.Label>
                    <Form.Control as={'input'} {...register('count')} />
                </Form.Group>
                <Form.Group>
                    <Form.Label column={true}>{t('calendar.cost')}</Form.Label>
                    <Form.Control as={'input'} disabled={true} value={data[0]?.price}/>
                </Form.Group>
                <Form.Group className={'my-3'}>
                    <Form.Check label={t('calendar.paid')} {...register('is_payed')} />
                </Form.Group>
            </Modal.Body>
            <Modal.Footer>
                <Button onClick={() => {
                    reset()
                    onClose()
                }} variant={'danger'}>{t('calendar.cancel')}</Button>
                <Button onClick={handleSubmit(handleSave)} variant={'success'}>{t('calendar.save')}</Button>
            </Modal.Footer>
        </Modal>
    )
}