import {createContext, useContext, useState} from "react";
import {Alert, Spinner} from "react-bootstrap";
import "./LoaderContext.css";

const LoaderContext = createContext({})

const LoaderProvider = ({children}:{children: any}) => {
    const [loader, setLoader] = useState(false)
    const [message, setMessage] = useState({type: "", text: ""})

    return (
        <LoaderContext.Provider value={{
            loader: loader,
            setLoader: setLoader,
            setMessage: setMessage
        }}>
            <div className={["loader", loader ? "d-flex" : "d-none"].join(" ")}>
                <Spinner variant={"primary"} animation={"border"} role={"status"} />
            </div>
            {message?.text && <Alert variant={message?.type}>{message?.text}</Alert>}
            {children}
        </LoaderContext.Provider>
    )
}

const useLoaderContext = () => useContext(LoaderContext)

export {LoaderContext, LoaderProvider, useLoaderContext}