import React from "react";
import {useTranslation} from "react-i18next";
import {useCalendarContext} from "../../hooks/CalendarContext";
import {Left, Right} from "../Icon";
import {Item} from "./";

const CalendarHeader = React.memo(({days}) => {
    const {t} = useTranslation();
    const {month, year, prevMonth, nextMonth} = useCalendarContext();
    const monthNames = t("months", {returnObjects: true});

    return (
        <thead>
        <tr key="header">
            <td rowSpan={2} className="vertical-middle">
                <div className="button" onClick={prevMonth}>
                    <Left color="#000" size={24}/>
                    <span>{t("calendar.back")}</span>
                </div>
            </td>
            <td colSpan={days}>
                <span>{monthNames[month]} {year}</span>
            </td>
            <td rowSpan={2} className="vertical-middle">
                <div className="button" onClick={nextMonth}>
                    <span>{t("calendar.next")}</span>
                    <Right color="#000" size={24}/>
                </div>
            </td>
        </tr>
        <tr key="days">
            {Array.from({length: days}, (_, i) => (
                <Item key={`${year}-${month}-${i}`} day={i + 1}/>
            ))}
        </tr>
        </thead>
    );
});

export {CalendarHeader}