import React, {useCallback, useEffect, useMemo} from "react";
import {useCalendarContext} from "../../hooks/CalendarContext";
import {Helper} from "../../constants/Helper";
import {useTranslation} from "react-i18next";
import i18next from "../../constants/Locale";

const CalendarFooter = React.memo(({data, days, setHoverData, isFinance}) => {
    const {month, year, language} = useCalendarContext();
    const period = `${year}-${Helper.zeroPad(month + 1, 2)}`;
    const {t} = useTranslation();
    useEffect(() => {
        i18next.changeLanguage(language)
    }, [language]);
    // Оптимизированный расчет сумм
    const totals = useMemo(() => {
        return data?.reduce((acc, curr) => ({
            debit_hours: acc.debit_hours + Number(curr.debit_hours),
            credit_hours: acc.credit_hours + Number(curr.credit_hours),
            debit_amount: acc.debit_amount + Number(curr.debit_amount),
            credit_amount: acc.credit_amount + Number(curr.credit_amount)
        }), {debit_hours: 0, credit_hours: 0, debit_amount: 0, credit_amount: 0}) || {};
    }, [data]);

    const handleHover = useCallback((type) => (e) => {
        const {left, top, height} = e.target.getBoundingClientRect();
        const hoverData = {
            position: {x: left - 280, y: top - 60 + height / 2},
            bg: type === 'debit' ? "bg-success" : type === 'credit' ? "bg-danger" : "bg-secondary",
            user_id: 0,
            date: period,
            ...(type === 'debit' ? {
                count: totals.debit_hours,
                sum: totals.debit_amount,
                is_payed: 1
            } : type === 'credit' ? {
                count: totals.credit_hours,
                sum: totals.credit_amount,
                is_payed: 0
            } : {
                count: totals.debit_hours + totals.credit_hours,
                sum: totals.debit_amount + totals.credit_amount,
                is_payed: null
            })
        };
        setHoverData(hoverData);
    }, [totals, period, setHoverData]);

    return (
        <tfoot>
        <tr>
            <td colSpan={days + 1} style={{paddingRight: 10}}>
                {t("calendar.total")}
            </td>
            <td className="final">
                <div className="summary-cell">
                    <div
                        className="is-payed"
                        onMouseOver={handleHover('debit')}
                        onMouseLeave={() => setHoverData(null)}
                    >
                        {isFinance ? totals.debit_amount : totals.debit_hours}
                    </div>
                    <div
                        className="un-payed"
                        onMouseOver={handleHover('credit')}
                        onMouseLeave={() => setHoverData(null)}
                    >
                        {isFinance ? totals.credit_amount : totals.credit_hours}
                    </div>
                    <div
                        className="total"
                        onMouseOver={handleHover('total')}
                        onMouseLeave={() => setHoverData(null)}
                    >
                        {isFinance
                            ? totals.debit_amount + totals.credit_amount
                            : totals.debit_hours + totals.credit_hours}
                    </div>
                </div>
            </td>
        </tr>
        </tfoot>
    );
});

export {CalendarFooter}